* {
    padding: 0;
    margin: 0;
}

a, a:hover {
    text-decoration: none;
}


html, body {
    height: 100%;
}

html {
    scroll-behavior: smooth;
}

body {
    background: fixed -webkit-linear-gradient(bottom, black, #004764, #308398) center;
    background: fixed linear-gradient(to top, black, #004764, #308398) center;
}

#root {
    height: 100%;
    max-width: 1100px;
    margin: 0 auto;
}

.inRoot {
    position: relative;
    height: 100%;
    display: grid;
    grid-template-rows: 100px auto 60px;
}

header {
    width: 100%;
    position: relative;
    display: flex;
    align-items: flex-end;
}

.movieName {
    color: red;
    letter-spacing: 10px;
    font-size: 35px;
    font-weight: bold;
    margin: 0 0 20px 20px;
    text-shadow: 2px 2px 10px black;
}

.movieName:hover {
    color: red;
}

.navPanel {
    margin: 0 0 20px 20px;
    font-size: 14px;
}

.navPanel a{
    text-shadow: 2px 2px 10px black;
    color: white;
    margin: 0 10px;
    text-transform: uppercase;
    letter-spacing: 5px;
    padding: 10px 0;
    position: relative;
    -moz-transition: ease-out 0.3s 0.1s;
    -o-transition: ease-out 0.3s 0.1s;
    -webkit-transition: ease-out 0.3s;
    -webkit-transition-delay: 0.1s;
    -webkit-transition: ease-out 0.3s 0.1s;
    transition: ease-out 0.3s 0.1s;
}

.navPanel a::after {
    height: 2px;
    width: 100%;
    background: white;
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.navPanel a:hover::after {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
}

footer {
    display: flex;
    justify-content: center;
    width: 100%;
    align-self: center;
}


@media only screen and (max-width: 700px) {
    header {
        display: flex;
        flex-direction: column;
        align-items: baseline;
    }

    .movieName {
        margin: 28px 0 0 20px;
    }
}

.content {
    margin-top: 50px;
    position: relative;
    max-width: 1100px;
}

.trailer_poster {
    height: 500px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.trailer iframe {
    border: none;
    width: 640px;
    height: 480px;
}

.poster {
    height: 480px;
    box-shadow: 2px 2px 10px black;
    cursor: pointer;
}

.poster img {
    max-height: 100%;
    max-width: 100%;
}

.home_slider {
    margin-top: 80px;
    height: 300px;
    display: flex;
    align-items: center;
    flex-direction: row;
    position: relative;
    justify-content: center;
    overflow: hidden;
}

.label {
    position: absolute;
    top: 0;
    left: 60px;
    color: white;
    font-size: 34px;
    letter-spacing: 5px;
    text-shadow: 2px 2px 10px black;
    text-transform: uppercase;

}

.arrow {
    position: absolute;
    /*padding: 50px; !* Arrow size *!*/
    width: 130px;
    height: 130px;
    box-shadow: 1px -1px 0 1px rgba(255,255,255, 0.1) inset;
    -webkit-box-shadow: 2px -2px rgba(255,255,255, 0.1) inset;
    border: solid transparent;
    border-width: 0 0 2rem 2rem;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    cursor: pointer;
    top:0;
    bottom: 0;
    margin: auto;
}

.arrow:hover {
    box-shadow: 2px -2px 0 2px white inset;
    -webkit-box-shadow: 4px -4px white inset;
}

.arrow-right {
    -webkit-transform:rotate(225deg);
            transform:rotate(225deg);
    position: absolute;
    right: 0;
}
.arrow-left  {
    -webkit-transform:rotate(45deg);
            transform:rotate(45deg);
    position: absolute;
    left: 0;
}

.heroesList {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 100%;
}

.heroImg {
    height: 200px;
    width: 200px;
    cursor: pointer;
    margin: 0 20px;
    border-radius: 50%;
    background-size: cover !important;
    background-position: center !important;
    -webkit-transition: box-shadow 0.8s;
    transition: box-shadow 0.8s;
}

.heroImg:hover {
    box-shadow: 0 0 20px black;
}

.info_list {
    display: flex;
    flex-direction: row;
    color: white;
}

.infoItem {
    height: 200px;
    width: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 20px;
    cursor: pointer;
    -webkit-transition: 0.8s;
    transition: 0.8s;
    color: white;
}

.infoItem div{
    width: 100%;
    height: 100%;
    background-size: cover !important;
    background-position: center !important;
    -webkit-transition: box-shadow 0.8s;
    transition: box-shadow 0.8s;
}

.infoItem:hover div{
    box-shadow: 0 0 20px black;
}

.infoItem:hover{
    text-shadow: 2px 2px 10px black;
    color: white;
}

@media only screen and (max-width: 1100px) {
    .trailer_poster {
        height: 1000px;
        flex-direction: column;
    }

    .trailer {
        order: 2;
    }

    .trailer iframe {
        width: 640px;
        max-width: 100%;
    }

}

@media only screen and (max-width: 900px) {
    .home_slider {
        margin-top: 30px;
        height: auto;
        padding-top: 30px;
    }

    .heroesList {
        display: grid;
        height: 200px;
        grid-template-columns: 220px 220px;
        overflow: hidden;
    }

    .info_list {
        /*padding-top: 30px;*/
        display: grid;
        height: 200px;
        grid-template-columns: 220px 220px;
        overflow: hidden;
    }
}

@media only screen and (max-width: 700px) {
    .home_slider {
        /*padding-top: 50px;*/
        height: 300px;
    }

    .heroesList {
        grid-template-columns: 200px;
    }

    .heroImg {
        margin: 0;
    }

    .info_list {
        grid-template-columns: 220px;
    }

    .infoItem {
        margin: 0 10px;
    }
}
.movie_about {
    margin-top: 80px;
    display: grid;
    align-items: center;
    flex-direction: column;
    position: relative;
    color: white;
    letter-spacing: 1px;
}

.movie_poster_desc {
    display: grid;
    grid-template-columns: 480px auto;
    margin-top: 70px;
    text-align: justify;
}

.movie_poster_desc p{
    padding: 10px 10px 0 10px;
    white-space: pre-line;
}

.movie_poster_desc_poster {
    margin-left: 100px;
    width: 329.47px;
}

.movie_moreInfo {
    margin-left: 100px;
}

.movie_moreInfo ul {
    margin-top: 30px;
}

.movie_moreInfo ul li{
    list-style: none;
    display: grid;
    grid-template-columns: 150px auto;
}

.movie_moreInfo ul li a {
    color: white;
}

.movie_scenes {
    position: relative;
    margin-top: 80px;
    height: 300px;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
    overflow: hidden;
}

.movie_scenes_all {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 100%;
}

.sceneItem {
    height: 200px;
    width: 200px;
    margin: 0 20px;
    cursor: pointer;
    position: relative;
    z-index: 0;
}

.biggerImg {
    position: absolute;
    padding: 10px;
    box-shadow: 1px -1px 0 1px rgba(255,255,255, 0.1) inset;
    -webkit-box-shadow: 2px -2px rgba(255,255,255, 0.1) inset;
    border: solid transparent;
    border-width: 0 0 1rem 1rem;
    -webkit-transition: 0.8s;
    transition: 0.8s;
    z-index: 1;
}

.sceneItem:hover .biggerImg {
    box-shadow: 2px -2px 0 2px white inset;
    -webkit-box-shadow: 4px -4px white inset;
}

.biggerImg_right {
    -webkit-transform:rotate(180deg);
            transform:rotate(180deg);
    position: absolute;
    right: 0;
    top: 0;
}

.biggerImg_left {
    position: absolute;
    left: 0;
    bottom: 0;
}

.gallery {
    width: 100%;
    height: 100%;
    position:fixed;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    color: white;
    font-size: 30px;
}

.shadow {
    background: black;
    opacity: 0.8;
    width: 100%;
    height: 100%;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
}

.gallery i {
    z-index: 2;
    position: fixed;
    top: 20px;
    right: 20px;
    color: rgba(255,255,255, 0.1);
    cursor: pointer;
    -webkit-transition: 0.8s;
    transition: 0.8s;
}

.gallery i:hover {
    color: white;
}

.gallery_image {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    top:0;
    bottom: 150px;
    left: 0;
    right: 0;
}

.gallery_image img {
    max-width: 100%;
    max-height: 100%;
    margin: 70px auto 0 auto;
}

.arrow_holder {
    position: absolute;
    height: 100%;
    width: 120px;
    display: flex;
    align-items: center;
    -webkit-transition: 0.8s;
    transition: 0.8s;
    cursor: pointer;
}

.arrow_holder:hover > .arrow{
    box-shadow: 2px -2px 0 2px white inset;
    -webkit-box-shadow: 4px -4px white inset;
}

.arrow_holder:hover{
    background-color: rgba(255,255,255, 0.1);
}

.allScenery {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 150px;
    background-color: black;
    display: -webkit-box;
    -webkit-box-align: center;
    overflow-x: scroll;
}

.galleryItem {
    width: 100px;
    height: 100px;
    margin: 0 10px;
    cursor: pointer;
    -webkit-transition: 0.8s;
    transition: 0.8s;
}

.galleryItem:hover {
    height: 120px;
    width: 120px;
}

.activeImg {
    border: 2px solid red;
}

.movie_film {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 500px;
    position: relative;
    margin-top: 80px;
}

.movie_film iframe {
    width: 90%;
    height: auto;
    border: none;
}

.movie_moreInfo ul li div a{
    color: white;
    padding: 2px 0;
    position: relative;
    -moz-transition: ease-out 0.3s 0.1s;
    -o-transition: ease-out 0.3s 0.1s;
    -webkit-transition: ease-out 0.3s;
    -webkit-transition-delay: 0.1s;
    -webkit-transition: ease-out 0.3s 0.1s;
    transition: ease-out 0.3s 0.1s;
}

.movie_moreInfo ul li div a::after {
    height: 2px;
    width: 100%;
    background: white;
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.movie_moreInfo ul li div a:hover::after {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
}

